.navigation-container-actes{
    margin-left: 54px;
    width: 670px;
    left: 0;
    ul{
        display: flex;
        justify-content: space-between;
        align-items: center;

        a{
            color: $black;
            transition: 0.3s;
            &:hover{
                color: $green;
            }
           
            position: relative;
            li{
                font-size: 1.2rem;
                 
    
                span{
                    position: absolute;
                    transform: translate(22px,-2.5px);
                    color: $black;
                    font-size: 1.1rem;
    
                }
            }

        }

        

       
    }
}

.container-actes{
    margin: 70px 54px ;
    display: grid;
    grid-template-columns: 70% 30%;
    
    .text-container{

        .Pathologies-traitées{

        .title-1{
            width: 360px;
            padding-top: 50px;

            h3{
                margin-top: 100px;
                position: relative;
                color: $green;
                font-size: 2.5rem;

                &::after{
                    content: "";
                    width: 100%;
                    height: 5px;
                    position: absolute;
                    transform: translateY(-50px);
                    background: $red;
                    left: 0;
                }
            }
           }

          
           
           
        }
        .Actes-Therapeutiques{
            .title-2{
                width: 400px;
    
                h3{
                    margin-top: 100px;
                    position: relative;
                    color: $green;
                    font-size: 2.5rem;
                    
    
                    &::after{
                        content: "";
                        width: 100%;
                        height: 5px;
                        position: absolute;
                        transform: translateY(-45px);
                        background: $red;
                        left: 0;
                    }
                }
                
    
            }

        }

    }

    .raccourci-container-actes{
        padding-top: 80px;

        .raccourci{
            width: 270px;
            margin-left: 90px;
            border-left: 1px solid $red;
            border-bottom: 1px solid $red ;
            box-shadow: -2px 2px 4px $red;
            

            h3{
                font-size: 2rem;
                color: $green;
                text-align: center;
            }
            ul{
                margin: 30px 0 30px 10px;

                a{
                    border-bottom: 1px solid rgb(80, 75, 75);
                    display: block;


                    &:hover::after{
                        content: "cliquez pour accedez";
                        color: $red;
                        position: absolute;
                        transform: translate(100px,-56px);
                    }

                    li{
                        font-size: 1.3rem;
                        color: $black;
                        padding: 18px 0;
                    }
                }
            }

        }
       
    }

}