main{
    
    max-width: 1100px;
    margin: 0 auto;
    

    .clinique-resume{
        padding-top: 155px;

        h1{
            font-size: 4rem;
            color: $green;
            padding-bottom: 35px;
        }
        .grid-container{
            display: grid;
            grid-template-columns: repeat(3,365px);
            grid-template-rows: repeat(3,140px);

            span:nth-child(1){
                background: $green;
                font-size: 2.5rem;
                font-weight: bolder;
                padding: 4px ;
                color: $black;
                padding-top: 10px;
            }
            span:nth-child(5){
                background: #004c99;
                font-size: 2.5rem;
                font-weight: bolder;
                text-align: center;
                color: $white;
                text-align: center;
                padding-top: 19px;
            }
            span:nth-child(9){
                background: red;
                font-size: 2.5rem;
                font-weight: bolder;
                text-align: center;
                color: $green;
                padding-top: 19px;

            }

        }
        .introduction{
            padding-top: 64px;
            display: grid;
            grid-template-columns: 50% 50%;
            height: 560px;
            max-height: 561px;
            
            .static-text{
                height: 436px;
                background: rgb(247,247,247);

               h1{
                text-transform: uppercase;
                padding: 10px;
               }
               p{
                padding: 0 28px 0 10px;
                color: $black;
                font-size: 0.9rem;
               }

            }

        }
        .localisation{
            .text-container{
                display: flex;
                justify-content: center;
                align-items: center;
                text-align: center;
                height: 200px;
                width: 800px;
                margin: 0 auto;
                background: $yellow;
                box-shadow: 6px 6px 10px  $black;
                margin: 50px auto;
                span{
                   
                    color: $white;
                    font-size: 2rem;
                    font-weight: bold;
                    border: 1px solid $green;
                    padding: 15px 30px;
                    background: rgb(0,66,115);
                    border-radius: 30px;
                    cursor: pointer;
                    transition: 0.3s;

                    &:hover{
                        transform: scale(1.1);
                        background: $white;
                        color: $green;
                    }
                   
                    
                }
            }
            .coordonner{
                display: grid;
                grid-template-columns: 1fr 1fr 1fr;
                background: rgb(247,247,247);
                gap: 20px;

                .coordonner-container{
                    padding:80px;

                    .number{
                        background: $white;
                        text-align: center;
                        margin: 0 auto;
                        border: 1px solid $green;
                        box-shadow: 4px 4px 6px $black;
                        height: 281px;

                        h3{
                            text-transform: uppercase;
                            font-size: 1.1rem;
                            padding: 35px;
                            font-weight: bold;
                        }
                        span{
                            line-height: 25px;
                        }
                    }
                    .horaire{
                        background: $white;
                        text-align: center;
                        margin: 0 auto;
                        border: 1px solid $green;
                        box-shadow: 4px 4px 6px $black;

                        h3{
                            text-transform: uppercase;
                            font-size: 1.1rem;
                            padding: 35px;
                            font-weight: bold;
                            
                        }
                        span{
                            line-height: 25px;

                            em{
                                color: $green;
                            }
                        }

                    }
                }
               
            }
           
           
        }
      
    }

}