.form-container{
    margin: 0 auto;
    text-align: center;
          

        .container{
            padding: 200px 0;
            display: block;

            h2{
                font-size: 2rem;
               
            }
            .form-content{
                margin-top: 60px;
                
            label{
                font-weight: bold;
                font-size: 1.3rem;
                margin: 10px 0;
    
            }
            #name{
                width: 300px;
                height: 40px;
                border: 1px solid $green;
                border-radius: 3px; 
                margin: 10px 0; 
                
            }
            #email{
                width: 300px;
                height: 40px;
                border: 1px solid $green;
                border-radius: 3px; 
                margin: 10px 0;

            }
            #mess{
                width: 300px;
                height: 200px;
                border: 1px solid $green;
                border-radius: 3px; 

            }
            .hover{
                margin-top: 10px;
                padding: 10px 40px;
                background: $green;
                border: 1px solid $green;
                border-radius: 20px; 
                transition: 0.4s;
                color: $white;

                &:hover{
                    background: $white;
                    color: $green;
                }

            }
           

            }


        }
       
    
  
}

.success{
    color: $white;
    background: $green;

}
.error{
    color: $white;
    background: $red;
}