.down-header-emergency{
    clip-path: polygon(0% 0%, 0% 64%, 8% 76%, 21% 85%, 31% 90%, 46% 91%, 57% 88%, 71% 84%, 77% 84%, 88% 84%, 93% 82%, 100% 80%, 100% 0%);
    height: 240px;
    background: red;
    text-align: center;
    color: $white;

    h1{
        padding-top: 120px;
        font-size: 3rem;
    }
}

.navigation-container-emergency{
    margin-left: 54px;
    width: 770px;
    left: 0;
    ul{
        display: flex;
        justify-content: space-between;
        align-items: center;

        a{
            color: $black;
            transition: 0.3s;
            &:hover{
                color: $green;
            }
           
            position: relative;
            li{
                font-size: 1.2rem;
                 
    
                span{
                    position: absolute;
                    transform: translate(22px,-2.5px);
                    color: $black;
                    font-size: 1.1rem;
    
                }
            }

        }

        

       
    }
}

.container-emergency{
    margin: 70px 54px ;
    display: grid;
    grid-template-columns: 70% 30%;

    .text-container{
        h1{
            font-size: 4rem;
            color: red;
        }
        p{
            margin: 30px 0;
            font-size: 1.4rem;
            line-height: 30px;
        }
    }
}
.raccourci-container{
    padding-top: 80px;

    .raccourci{
        width: 270px;
        margin-left: 90px;
        border-left: 1px solid $red;
        border-bottom: 1px solid $red ;
        box-shadow: -2px 2px 4px $red;
        

        h3{
            font-size: 2rem;
            color: $green;
            text-align: center;
        }
        ul{
            margin: 30px 0 30px 10px;

            a{
                border-bottom: 1px solid rgb(80, 75, 75);
                display: block;


                &:hover::after{
                    content: "cliquez pour accedez";
                    color: $red;
                    position: absolute;
                    transform: translate(100px,-56px);
                }

                li{
                    font-size: 1.3rem;
                    color: $black;
                    padding: 18px 0;
                }
            }
        }

    }
   
}
