.dynamique-text{
    background: rgb(0,146,193);
    text-align: center;
    font-size: 32px;
    transition: 0.4s;
    padding: 10px 68px 0;
    z-index: -100;
    
}
.dynamic{
   

    #text-target{
        text-align: center;
        font-size: 34px;
        color: $white;
        padding-top: 100px;
        text-align: justify;
        word-spacing:5px;
        line-height: 32px;

        .word{
            line-height: 50px;
            word-spacing:1px;
            text-align: justify;
        }
       
        
       
    }
    .fa-3x{
        font-size: 1.5rem;
    }
}