.footer{
    background: $green;
    .footer-container{
        padding: 40px 40px;
        display: grid;
        grid-template-columns: 33% 33% 33% ;

        .contact{
            display: flex;
            flex-direction: column;

            h2{
                color: $white;
                font-size: 2.1rem;
            }
            p{
                margin: 10px 0;
                color: $white;
                font-size: 1.1rem;
            }
            button{
                background: none;
                border: 1px solid $black;
                border-radius: 30px;
                padding: 10px 45px;
                width: 200px;
                margin-bottom: 8px;
                color: $white;
            }

        }
        .social{
            display: flex;
            justify-content: space-evenly;
            align-items: center;
            padding-right: 120px;

            a{
                display: flex;
                justify-content: center;
                align-items: center;
                border: 1px solid $black;
                border-radius: 100px;
                padding: 15px;
                
                li{
                    color: $white;
                    font-size: 2rem;

                    &:hover{
                        color: $green;
                    }
                }
                &:hover{
                    background: $white;
                    
                }
            }
        }

        .country{
            display: flex;
            justify-content: space-evenly;
            align-items: center;
            .img-contain{
                width: 120px;
                height: 65px;
                
                img{
                   width: 100%;

                }
                
            }
            .minister-contain{
                width: 120px;
                height: 65px;
                   
                    img{
                        width: 100%;
                        height: 70px;
                    }

                    &:hover::after{
                        content: "ministere de la sante";
                        color: $white;
                    }

            }
            
        }
    }
}