.header{
    height: 521px;
    width: 100%;
    
}
.indicateur-home{
    padding: 40px 0 0 244px;

    a{
        li{
            font-size: 1.4rem;
            color: $black;
            
            span{
                padding:0 8px ;
            }
            &:hover{
                color: $green;
             }
        }
       
    }
}
header{
    width: 100%;
    display: grid;
    grid-template-columns: 30% 70%;
    align-items: center;
    width: 100%;
    height: 51px;
    background-color: rgba(255, 255, 255, 0.555) ;
    position: fixed;
    top: 0;
    z-index: 1000;
    
    


    .title-container{
        display: flex;
        justify-content: flex-end;
        align-items: center;
        color: $green; 

        .title{
            padding-right: 2px ;
            border-right: 1px solid rgb(170, 169, 163) ;
            
        }
       
        
    }
    

}