@font-face {
  font-family: "font-1";
  src: url(../assets/fonts/Questrial-Regular.ttf);
}
@font-face {
  font-family: "font-2";
  src: url(../assets/fonts/Michroma-Regular.ttf);
}

$font-1: "font-1", sans-serif;
$font-2: "font-2", serif;

$green: rgb(0,154,144);
$blue: #00c1ec;
$white: rgb(255,255,255);
$yellow: rgb(255,204,0);
$black: rgb(40, 40, 40);
$red: rgb(233,79,95);

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  list-style-type: none;
  outline: none;
 
  text-decoration: none;

  &::selection {
    color: $blue;
    background: $yellow;
  }
}
html { 
  scroll-behavior: smooth;
}
a,
a:visited {
  color: none;
}
body{
  font-family: $font-1;
}
