.navigation-container-guide{
    margin-left: 54px;
    width: 550px;
    left: 0;
    ul{
        display: flex;
        justify-content: space-between;
        align-items: center;

        a{
            color: $black;
            transition: 0.3s;
            &:hover{
                color: $green;
            }
           
            position: relative;
            li{
                font-size: 1.2rem;
                 
    
                span{
                    position: absolute;
                    transform: translate(22px,-2.5px);
                    color: $black;
                    font-size: 1.1rem;
    
                }
            }

        }  
    }
}

.container-guide{
    margin: 70px 54px ;
    display: grid;
    grid-template-columns: 70% 30%;

    .text-containner-guide{
        .Hospitalisation{
            h1{
                font-size: 4rem;
                color: $red;
            }
        }

        .Hospitalisation-complete{
           .title-guide{
                width: 480px;
                padding-top: 50px;

                h3{
                    margin-top: 100px;
                    position: relative;
                    color: $green;
                    font-size: 2.5rem;
                    
    
                    &::after{
                        content: "";
                        width: 100%;
                        height: 5px;
                        position: absolute;
                        transform: translateY(-50px);
                        background: $red;
                        left: 0;
                    }
                }
           }
           p{
            margin-top: 20px;
            font-size: 1.4rem;
            line-height: 30px;
           }

        }
        .hopital-de-jour{
            .title-guide{
                width: 280px;
                padding-top: 50px;

                h3{
                    margin-top: 100px;
                    position: relative;
                    color: $green;
                    font-size: 2.5rem;
                    
    
                    &::after{
                        content: "";
                        width: 100%;
                        height: 5px;
                        position: absolute;
                        transform: translateY(-50px);
                        background: $red;
                        left: 0;
                    }
                }

            }
            p{
                margin-top: 20px;
                font-size: 1.4rem;
                line-height: 30px;

            }

        }
        .Consultation{
            .title-guide{
                width: 280px;
                padding-top: 50px;

                h3{
                    margin-top: 100px;
                    position: relative;
                    color: $green;
                    font-size: 2.5rem;
                    
    
                    &::after{
                        content: "";
                        width: 100%;
                        height: 5px;
                        position: absolute;
                        transform: translateY(-50px);
                        background: $red;
                        left: 0;
                    }
                }


            }
            p{
                margin-top: 20px;
                font-size: 1.4rem;
                line-height: 30px;

            }

        }
    }

    .racourci-container-guide{
        padding-top: 80px;

        .raccourci{
            width: 270px;
            margin-left: 90px;
            border-left: 1px solid $red;
            border-bottom: 1px solid $red ;
            box-shadow: -2px 2px 4px $red;
            

            h3{
                font-size: 2rem;
                color: $green;
                text-align: center;
            }
            ul{
                margin: 30px 0 30px 10px;

                a{
                    border-bottom: 1px solid rgb(80, 75, 75);
                    display: block;


                    &:hover::after{
                        content: "cliquez pour accedez";
                        color: $red;
                        position: absolute;
                        transform: translate(100px,-56px);
                    }

                    li{
                        font-size: 1.3rem;
                        color: $black;
                        padding: 18px 0;
                    }
                }
            }

        }
       
    }


}