nav{
    padding-left: 200px;
    height: 100%;
    
    .navigation{
        width: 80%;
        height: 100%;
    
        ul{
            display: flex;
            justify-content: space-evenly;
            align-items: center;
            color: $black;
            height: 100%;

            a{
               
                border-right:1px solid $black ;
                

                li{
                    padding-right: 18px;
                    color: $black;
                    cursor: pointer;
                    transition: 1s;
                   

                    &:hover{
                        transform: scale(1.1);
                        color: $green;
                    }
                }
                
            }
            a:last-child{
                background: red;
                height: 100%;
                color: $white;
                margin:auto 0 ;
                text-align: center;
                display: flex;
                justify-content: center;
                align-items: center;
                padding-left: 8px;
                font-size: 1.1rem;

                &:hover{
                    background: $green;
                }

                li{
                    color: $white;
                }
                li:hover{
                    color: $black;
                }
            }
           
        }
    }

}
